export default function rootReducer(state = initState, action) {
    if (action.type === 'ADD_NOTE') {
        console.log(action)
        const {title, content, author, date} = action;
        let newPost = {
            id: state.notes.length + 1,
            title,
            content,
            author,
            date
        }
        return {
            notes: [...state.notes, newPost]
        }
    }
    console.log(action)
    return state;
};
