import React from "react";
import NoteForm from "./note-form/NoteForm";
import NoteList from "./note-list/NoteList";
import {Link, Route} from 'react-router-dom';
import NotePage from "./note-page/NotePage";


const App = () => {

    return (
        <>
            <nav className='p-3 mb-2 bg-info text-white sticky-top'>
                <div className='w-50 p-3 mx-auto d-flex justify-content-around'>
                    <Link className='text-white' style={{fontSize: "24px"}} to="/">Home</Link>
                    <Link className='text-white' style={{fontSize: "24px"}} to="/form">Create Form</Link>
                </div>
            </nav>

            <Route exact path="/" component={NoteList}/>
            <Route exact path="/form" component={NoteForm}/>
            <Route exact path="/note/:noteId" component={NotePage}/>
        </>
    )
}


export default App;

