import React, {useState} from 'react';
import {connect} from 'react-redux';
import {addNote} from "../../actions/noteActions";
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import {Helmet} from "react-helmet";

const NoteForm = (props) => {
    const {addNote, notes} = props;

    const [display, setDisplay] = useState(false);
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [author, setAuthor] = useState('');

    function titleHandler(e) {
        if (e.target.value.match(/^([^0-9]*)$/) != null) {
            setTitle(e.target.value)
        }
    }

    function contentHandler(e, editor) {
        setContent(editor.getData())
    }

    function authorHandler(e) {
        setAuthor(e.target.value)
    }


    function submitHandler(e) {
        e.preventDefault();
        const strippedTitles = notes.map(item => item.title);

        if (title === '' || author === '' || content === '') {
            alert('Please fill all fields!');
        } else if (strippedTitles.includes(title)) {
            alert('Note with this title already exists')
        } else {
            addNote(title, content, author);
            setTitle('');
            setContent('');
            setAuthor('');
        }
    }
    const setDex = (author) => {
        setAuthor(author);
        setDisplay(false);
    }
    return (<>
            <Helmet>
                <meta charSet="utf-8"/>
                <title>Form</title>
            </Helmet>
            <form className='w-50 p-3 mx-auto' onSubmit={(e) => submitHandler(e)}>
                <div className="form-group">
                    <input className="form-control" placeholder='Enter the title' value={title}
                           onChange={(e) => titleHandler(e)}/>
                </div>
                <div className="form-group">
                    <CKEditor editor={ClassicEditor} data={content}
                              onChange={(e, editor) => contentHandler(e, editor)}/>
                </div>
                <div className="form-group">
                    <input onClick={() => setDisplay(!display)}
                           className="form-control" placeholder='Enter your name' value={author}
                           onChange={(e) => authorHandler(e)}/>
                    {display && (
                        <div className="list-group mt-1">
                            {notes.map((item, index) => {
                                return (<div className="list-group-item list-group-item-light " key={index} onClick={() =>setDex(item.author)}>
                                    <span>{item.author}</span>
                                </div>)
                            })}
                        </div>
                    )}
                </div>
                <button onClick={() => setDisplay(!display)} className="btn btn-primary">Submit!</button>
            </form>


        </>
    )
}
const mapDispatchToProps = (dispatch) => {
    return {
        addNote: (title, content, author) => dispatch(addNote(title, content, author))
    }
}
const mapStateToProps = (state) => {
    return {
        notes: state.notes
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(NoteForm);