import React from 'react';
import {connect} from "react-redux";
import parse from 'html-react-parser';
import {Helmet} from "react-helmet";

const NotePage = ({match, notes}) => {
    let noteId = parseInt(match.params.noteId);

    notes = notes.find(item => {
        if (item.id === noteId) {
            return item
        }
    })

    return (<>
        <Helmet>
            <meta charSet="utf-8"/>
            <title>{notes.title}</title>
        </Helmet>
        <div className='card w-75 p-3 mx-auto'>
            <div className='card-body'>
                <h1 className="card-title">{notes.title}</h1>
                <div className="card-text mb-2">{parse(notes.content)}</div>
                <h5 className="card-subtitle mb-3 pt-3 text-muted">{notes.author}</h5>
                <h6 className='card-subtitle mb-3 text-muted'>{notes.date}</h6>
            </div>
        </div>
    </>)
}
const mapStateToProps = (state) => {
    return {
        notes: state.notes
    }
}
export default connect(mapStateToProps)(NotePage);