import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import parse from 'html-react-parser';
import {Helmet} from "react-helmet";

const Note = ({props}) => {

    return (
        <>
            <Helmet>
                <meta charSet="utf-8"/>
                <title>Home</title>
            </Helmet>
            <Link className=" list-group-item w-75 p-3 mx-auto" style={{ color: 'inherit', textDecoration: 'inherit'}} to={`/note/${props.id}`}>
            <li className=" list-group-item">
                <h4 className='mb-3'>{props.title}</h4>
                <p>{props.author}</p>
                <p className='text-muted'>Date: {props.date}</p>
            </li>
            </Link>
        </>
    )
}
const NoteList = ({notes}) => {
    return (
        <ul>
            {notes.map(item => {
                return (
                    <Note className='list-group' props={item} key={item.id}/>
                )
            })}
        </ul>
    )
}
const mapStateToProps = (state) => {
    return {
        notes: state.notes
    }
}
export default connect(mapStateToProps)(NoteList);